import React from "react";
import img from "../img/jurassic-park.jpg";
import { Container, Box, Typography, Stack, Button } from "@mui/material";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet";
import { set } from "react-hook-form";

function Detail() {
  const { movie } = useParams();
  let movieName = movie.toString().toLowerCase();
  movieName = movieName.replace(/ /g, "-");
  movieName = movieName.replace(/:/g, "");
  movieName = movieName.replace(/'/g, "");
  let [movieDetails, setMovieDetails] = useState([]);

  let [genre1, setGenre1] = useState("");
  let [genre2, setGenre2] = useState("");
  let [genre3, setGenre3] = useState("");

  const url = `https://api.andrespecht.dev/movie/${movieName}`;

  const options = {
    method: "GET",
    mode: "cors",
  };

  async function getMovies() {
    try {
      const response = await fetch(url);
      if (!response.ok) {
        throw new Error(`${response.statusText} (${response.status})`);
      }
      // Parsing the reponse as JSON
      const data = await response.json();
      // Printing the movies
      setMovieDetails(data.response);
      const genres = await data.response.genre;
      setGenre1(genres[0]);
      setGenre2(genres[1]);
      setGenre3(genres[2]);
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
    getMovies();
  }, []);

  return (
    <div>
      <Helmet>
        <title>{movie}</title>
      </Helmet>
      <Container>
        <Box
          sx={{
            height: "100svh",
            width: "100%",
            display: "flex",
            justifyContent: { xs: "flex-start", sm: "center" },
            alignItems: "flex-start",
            flexDirection: "column",
            textAlign: { xs: "center", sm: "left" },
          }}
        >
          <Box
            sx={{
              display: "flex",
              flexDirection: { xs: "column", sm: "row" },
              justifyContent: "center",
              alignItems: "center",
              mt: { xs: 5, sm: 0 },
              borderRadius: "10px",
            }}
          >
            <img
              src={movieDetails.poster}
              alt="test"
              className="detail-img"
              sx={{
                width: { xs: "100%", sm: "30%" },
                height: { xs: "auto", sm: "100%" },
                objectFit: "cover",
              }}
            ></img>
            <Stack
              spacing={2}
              sx={{
                ml: { xs: "0", sm: 3 },
                justifyContent: { xs: "center", sm: "left" },
              }}
            >
              <Typography
                variant="h4"
                component="div"
                sx={{
                  mt: 2,
                }}
              >
                {movieDetails.title}
              </Typography>
              <Stack
                spacing={2}
                direction={"row"}
                sx={{
                  justifyContent: { xs: "center", sm: "left" },
                }}
              >
                <Typography variant="h6" component="div">
                  {movieDetails.year}
                </Typography>
                <Typography variant="h6" component="div">
                  {movieDetails.runningTime}
                </Typography>
                <Typography variant="h6" component="div">
                  4k
                </Typography>
              </Stack>
              <Stack
                spacing={2}
                direction={"row"}
                sx={{
                  justifyContent: { xs: "center", sm: "left" },
                }}
              >
                <Typography variant="h6" component="div">
                  {genre1}
                </Typography>
                <Typography variant="h6" component="div">
                  {genre2}
                </Typography>
                <Typography variant="h6" component="div">
                  {genre3}
                </Typography>
              </Stack>
              <Typography variant="h6">{movieDetails.description}</Typography>
              <Box>
                <Button
                  variant="contained"
                  sx={{
                    maxWidth: "135px",
                  }}
                >
                  Watch now
                </Button>
              </Box>
            </Stack>
          </Box>
        </Box>
      </Container>
    </div>
  );
}

export default Detail;
